@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: rgb(80, 54, 228);
}

ul {
  list-style: none;
}

a {
  color: rgb(140, 0, 255);
  text-decoration: none;
}

button {
  padding: 1rem;
  height: 5rem;
  border: 1px solid #ccc;
  font-size: 1.3rem;
  cursor: pointer;
}

button:hover {
  opacity: 0.7;
  border: 1px solid rgb(99, 97, 97);
}

button:nth-child(17) {
  grid-column: 1/3;
}

button:nth-child(4n),
button:last-child:hover {
  background-color: #b56fca;
}

.calculator-result {
  background-color: grey;
  text-align: end;
  padding: 1rem 0.2rem;
  color: #fff;
  font-size: 22px;
  box-shadow: #e00fa1 0 20px 30px -10px;
}

.calculator-buttons {
  display: grid;
  grid-template: repeat(5, 1fr) / repeat(4, 1fr);
  box-shadow: #e00fa1 0 20px 30px -10px;
}

button {
  padding: 1rem 1.6rem;
  border: 1px solid rgba(195, 195, 195, 0.4);
}

button:hover {
  color: aqua;
}

.zero {
  grid-column: span 2;
}

.pink {
  background-color: #b85d8a;
  border: 1px solid rgba(212, 212, 212, 0.75);
}

nav {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

nav ul {
  width: 200px;
  display: flex;
  justify-content: space-between;
}

.title {
  font-size: 48px;
  color: #6c5ce7;
}

nav {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

nav ul {
  width: 260px;
  display: flex;
  justify-content: space-between;
}

nav li {
  font-size: 22px;
}

nav li:hover {
  font-size: 25px;
  background-color: rgb(114, 204, 114);
}

.middle {
  border-left: 2px solid blue;
  border-right: 2px solid blue;
  padding: 0 0.8rem;
}

.home-holder {
  padding: 0 5rem;
  margin-top: 3rem;
}

.home-title {
  font-size: 26px;
  color: rgb(255, 80, 217);
}

.home-title:hover {
  font-size: 30px;
  color: rgb(102, 189, 22);
}

.home-text {
  margin-left: 3rem;
  margin-top: 5rem;
  font-size: 20px;
}

.home-second-text {
  margin-left: 3rem;
  margin-top: 2rem;
  font-size: 20px;
}

.calc-holder {
  display: flex;
  justify-content: space-between;
  padding: 0 5rem;
  margin-top: 2rem;
}

.calculator {
  width: 500px;
}

.sub-title {
  font-size: 26px;
}

.sub-title:hover {
  font-size: 30px;
  color: rgb(182, 17, 174);
}

.quote-section {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  grid-gap: 40px;
  gap: 40px;
  padding: 10%;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 160px;
}

.quote {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  font-size: 30px;
  text-align: center;
  line-height: 56px;
  font-style: italic;
  color: #a035a3;
  font-family: "Playfair Display", serif;
}

.line {
  height: 4px;
  background-color: #f116c2;
  width: 30%;
  align-self: center;
}

